import React from 'react';
import '../css/ribbon3.css';

function Ribbon3() {
	return (
		<div class="arrow">
			<div class="arrow_main arrow_main1 arrow_active"><span className='elipse'>1</span></div>
			<div class="arrow_main arrow-second"><span className='elipse'>2</span></div>
			<div class="arrow_main arrow-second"><span className='elipse'>3</span></div>
			<div class="arrow_main arrow-last"><span className='elipse'>4</span></div>
		</div>

	)
}

export default Ribbon3;



