import React from 'react';
import tv from '../img/tv.png';
import home from '../img/home.png';
import earth from '../img/earth.png';
import '../css/howWorking.css';


function HowWorking() {
	return (
		<section className='how-working' id="howWorking">
			<div className='how-working__container'>
				<h2 className='how-working__title titleh2'>  </h2>
				<button className='how-working__button'> </button>
				<div className='how-working__content'>
					<div className='how-working__card'>
						<div className='how-working__card-img'>
							<img src={tv}></img>
						</div>
						<div className='how-working__card-text'>  <br />  </div>
					</div>
					<div className='how-working__card'>
						<div className='how-working__card-img'>
							<img src={home}></img>
						</div>
						<div className='how-working__card-text'>  <br />   </div>
					</div>
					<div className='how-working__card'>
						<div className='how-working__card-img'>
							<img src={earth}></img>
						</div>
						<div className='how-working__card-text'>  <br /></div>
					</div>
				</div>
			</div>
		</section>
	)
}

export default HowWorking;