import React from 'react';
import { BrowserRouter as Router, Link } from "react-router-dom";
import cashed from '../img/ic_cached_white_48dp.png';
import '../css/formRoute.css';

function FormRoute() {
	return (
		<form className="form-router" action="#" method="get">
			<label htmlFor name="route"></label>
			<div className='select-grup'>
				<select name="route" className="select-router router-svg">
					<option value="placeholder" className="placehold" disabled selected hidden></option>
					<option value="moscow"></option>
					<option value="angarsk"></option>
					<option value="arhangelsk"></option>
					<option value="astrahan"></option>
					<option value="barnaul"></option>
					<option value="belgorod"></option>
					<option value="blagoveshchensk"></option>
					<option value="bratsk"></option>
					<option value="briansk"></option>
					<option value="vielikijnovgorod"> </option>
				</select>

				<div className="cashed">
					<img src={cashed} className="logo"></img>
				</div>

				<select name="route" className="select-router">
				<option value="placeholder" className="placehold" disabled selected hidden></option>
					<option value="moscow"></option>
					<option value="angarsk"></option>
					<option value="arhangelsk"></option>
					<option value="astrahan"></option>
					<option value="barnaul"></option>
					<option value="belgorod"></option>
					<option value="blagoveshchensk"></option>
					<option value="bratsk"></option>
					<option value="briansk"></option>
					<option value="vielikijnovgorod"> </option>
				</select>
			</div>

			<label htmlFor name="date"></label>
			<div className='select-grup'>
				<input name="date" type="date" className="select-router" />
				<input name="date" type="date" className="select-router" />
			</div>

			<div className='button'>				
				<Link to="/TrainSelection" className='button-router'> </Link>				
			</div>
		</form>
	)
}

export default FormRoute;