import React from 'react';
import { Pagination, Keyboard, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import '../css/reviews.css';
import katia from '../img/katia.png';
import jenia from '../img/jenia.png';

const data = [
	{
		id: 1,
		username: ' ',
		photo: katia,
		testimonial: '"               / ,       ."',
	},
	{
		id: 2,
		username: ' ',
		photo: jenia,
		testimonial: '"-   c   /    3       -  ."',
	},
	{
		id: 3,
		username: ' ',
		photo: katia,
		testimonial: '"               / ,       ."',
	},
	{
		id: 4,
		username: ' ',
		photo: jenia,
		testimonial: '"-   c   /    3       -  ."',
	},
	{
		id: 5,
		username: ' ',
		photo: katia,
		testimonial: '"               / ,       ."',
	},
	{
		id: 6,
		username: ' ',
		photo: jenia,
		testimonial: '"-   c   /    3       -  ."',
	},

]


function Reviews() {
	return (
		<section className="reviews" id="reviews">
			<div className='reviews__container'>
				<h2 className="reviews__title titleh2"></h2>
				<div className="reviews-slider swiper-container">
					<div className="reviews-slider__wrapper swiper-wrapper"></div>
					<Swiper
						// install Swiper modules
						modules={[Pagination, Keyboard, A11y]}
						spaceBetween={37}
						slidesPerView={2}
						pagination={{ clickable: true }}
						onSwiper={(swiper) => console.log(swiper)}
						onSlideChange={() => console.log('slide change')}
					>
						{data.map(user => (
							<SwiperSlide key={user.id} className="slide">
								<div className="slide-content">
									<div className="user-image">
										<img src={user.photo} alt={user.username} className="user-photo" />
									</div>
									<div className='user-content'>
										<h5 className="author">{user.username}</h5>
										<p className="user-testimonial">" <i>{user.testimonial}</i> "</p>
									</div>
								</div>
							</SwiperSlide>
						))}

					</Swiper>
				</div>
			</div>

		</section>
	);
};

export default Reviews;